





import Vue from 'vue'
import echarts from 'echarts'
export default Vue.extend({
  mounted(){
    let charts = echarts.init(this.$el);
    charts.setOption({
      series: [
        {
          type: 'gauge',
          data: [
            {
              name: 'a',
              value: 12
            }
          ]
        }
      ]
    })
  }
})
