




import Vue from 'vue'
import { WebGLRenderer, Scene, PerspectiveCamera, Camera } from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
export default Vue.extend({
  data(){
    return{
      renderer: WebGLRenderer,
      scene: Scene,
      control: OrbitControls
    }
  },
  mounted(){
    let renderer = new WebGLRenderer();
    renderer.setSize(400, 300);
    let scene = new Scene();
    let camera = new PerspectiveCamera(75, 1, 0.1, 1000);
    let control = new OrbitControls(camera, renderer.domElement);
    this.renderer = renderer;
    this.scene = scene;
    this.camera = camera;
    this.control = control;
    

    document.getElementById("test").appendChild(renderer.domElement);
    this.animation();
  },
  methods: {
    animation(){
      
      let renderer: WebGLRenderer = this.renderer;
      let scene: Scene = this.scene;
      let camera: Camera = this.camera;
      renderer.setClearColor("#ff00ff");
      renderer.clear(true, true, true);
      renderer.render(scene, camera);
      requestAnimationFrame(this.animation);
    }
  }
})
